    height: 45px;
    margin: 8px 25px;
    padding: 5px;
    background: linear-gradient(orange, LightSkyBlue);
    display: flex;
    justify-content: stretch;
    border-radius:20% ;
    -webkit-border-radius:20% ;
    -moz-border-radius:20% ;
    -ms-border-radius:20% ;
    -o-border-radius:20% ;
}
.skills_tech{
    /* border-style: dashed; */
    display:flex ;
    flex-wrap : wrap;
   text-align: left;
