}

p{
 font-size: 20px;
 text-align: justify;
 padding: 20px 20px;
 line-height: 27px;
 color: rgb(45, 30, 120);
 box-shadow: 0px 0px 5px 4px rgba(214,196,130,0.6);
 -webkit-box-shadow: 0px 0px 7px 1px rgba(214,196,130,0.6);
 -moz-box-shadow: 0px 0px 5px 4px rgba(214,196,130,0.6);
 border-radius: 5%;
 -webkit-border-radius: 5%;
 -moz-border-radius: 5%;
 -ms-border-radius: 5%;
 -o-border-radius: 5%;
}
